import React, { useRef, useState, useEffect } from 'react'
import { app } from '../firebaseinit'
import { getFirestore, doc, setDoc, getDoc, snapshotEqual } from 'firebase/firestore'
import { getStorage, uploadBytes, ref, getDownloadURL} from 'firebase/storage'

const MyPage = ({history}) => {
    const db = getFirestore(app);
    const storage = getStorage(app);

    const [file, setFile] = useState(null);
    const [form, setForm] = useState({
        email: sessionStorage.getItem('email'),
        name: '',
        address: '   ',
        phone: '010-0101-0101',
        photo:''
    });

    const {email, name, address, phone, photo} = form;
    const ref_file = useRef(null);
    const [image, setImage] = useState('');

    const onChageFile = (e) => {
        
        setImage(URL.createObjectURL(e.target.files[0]));
        setFile(e.target.files[0]);
    }
    const onSubmit = async(e) => {
        e.preventDefault();
        if(!window.confirm(' ?')) return;

        let url="";
        if(file !== null) { // 
            const result=await uploadBytes(ref(storage, `images/${Date.now()}_${file.name}`),file);
            url = await getDownloadURL(result.ref);
        }
        await setDoc(doc(db, 'users', email), {
            ...form,
            photo: url
        });
        history.push('/');
    }

    const onChange = (e) => {
        setForm({...form, [e.target.name]:e.target.value})
    }

    const onRead = async() => {
        const user=await getDoc(doc(db, 'users', email));
        console.log(user.data());
        setForm(user.data());
        setImage(user.data().photo);
    }

    useEffect(()=>{
        onRead();
    }, []);

    return (
        <div className='mypage'>
            <h1></h1>
            <form onSubmit={onSubmit}>
                <input onChange={onChange} 
                    name="name" value={name} placeholder=''/>
                <input onChange={onChange}
                    name="address" value={address} placeholder=''/>
                <input onChange={onChange}
                    name="phone" value={phone} placeholder=''/>
                <img src={image || 'http://via.placeholder.com/100x120'} 
                    onClick={()=>ref_file.current.click()}
                    style={{width:'50%'}}/>
                <input onChange={onChageFile}
                    ref={ref_file} 
                    type="file" accept='image/*' style={{display:'none'}}/>
                <hr/>
                <button></button>
            </form>
        </div>
    )
}

export default MyPage